module.exports = "import { Input } from 'react-ocean-forms';\n\nconst myRequiredValidator = (value) => {\n  return value === 'OK' ? undefined : 'Value is not OK!';\n};\nmyRequiredValidator.isDefaultValidator = true;\n\n<Input\n  name=\"demo\"\n  label=\"lbl_demo\"\n  validators={[myValidator]}\n/>\n"